import { useParams } from "react-router";
import { LoadPageAnimation } from "../reusable_components/load_page_animation";
import { useMutation } from "react-query";
import { resetPasswordFunc } from "../../api/api";
import { useState } from "react";
import { useForm } from "react-hook-form";
import { cn } from "../../utils/custom_functions";
import { Button } from "../reusable_components/custom_button";
import { ThreeDots } from "react-loader-spinner";
import { ErrorAlert, SuccessAlert } from "../reusable_components/alerts/alerts";
import { FaRegEyeSlash, FaRegEye } from "react-icons/fa6";
export default function ResetPassword() {
  const { token } = useParams();
  const [isPasswordVisible, setIspasswordVisible] = useState(false);
  const [disableUserInterface, setDisableUserInterface] = useState(false);

  // hooks
  const {
    register,
    handleSubmit,
    formState: {
      isLoading: isFormLoading,
      errors: formErrors,
      // isSubmitting: isFormSubmitting,
    },
  } = useForm();

  const {
    isLoading: isPasswordResetLoading,
    isSuccess: isPasswordResetSuccess,
    mutateAsync: resetPasswordFuncAsync,
    isError: isPasswordError,
    error: passwordError,
  } = useMutation({
    mutationFn: resetPasswordFunc,
    onSuccess: (data) => {},
    onMutate: (value) => {
      setDisableUserInterface((p) => true);
    },
    onError: (e) => {
      console.log(e);
      setDisableUserInterface((p) => false);
    },
  });

  const submitHander = async (data) => {
    try {
      data["token"] = token; // set token
      await resetPasswordFuncAsync(data);
    } catch (error) {
      // console.log(error);
    }
  };

  return (
    <LoadPageAnimation>
      <main className="max-w-xl mx-auto my-4 p-6 text-black bg-white">
        <h2 className="text-start text-black pb-6 font-extrabold text-3xl">
          Reset password
          <p className="text-sm font-normal text-slate-600 pt-2">
            Thank you for choosing Matols logistics services as your go to
            service provider.{" "}
            <span className="text-sm text-blue-700">
              Enter new password below, when finished submit the form
            </span>
            !.
          </p>
        </h2>
        <div>
          <form onSubmit={handleSubmit(submitHander)}>
            {/* {token} */}
            <div>
              <div className="relative">
                <input
                  {...register("password", {
                    required: "Enter your new password",
                    disabled: disableUserInterface,
                  })}
                  type={isPasswordVisible ? "text" : "password"}
                  name="password"
                  placeholder="Enter new password"
                  className={cn(
                    "peer text-sm pe-9 text-slate-800 w-full shadow-sm relative block rounded-md border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-75 disabled:pointer-events-none",
                    {
                      " text-slate-800 placeholder:text-slate-800 bg-red-100":
                        formErrors.password,
                    }
                  )}
                />
                <div className="peer-focus:z-10 absolute inset-y-0 right-0 px-3 flex justify-center items-center">
                  <div onClick={() => setIspasswordVisible((r) => !r)}>
                    {!isPasswordVisible ? (
                      <FaRegEyeSlash
                        className="text-yellow-400 cursor-pointer hover:opacity-75"
                        size={20}
                      />
                    ) : (
                      <FaRegEye
                        className="text-yellow-400 cursor-pointer hover:opacity-75"
                        size={20}
                      />
                    )}
                  </div>
                </div>
              </div>
              <span className="text-xs text-gray-500">
                Enter your new password, please make sure to remember this
                password.
              </span>
            </div>
            {formErrors.password && (
              <p className="pt-2 text-red-500 text-sm italic">
                {formErrors.password.message}
              </p>
            )}
            <div className="w-full py-4">
              {isPasswordError && (
                <ErrorAlert
                  className="mb-4"
                  secondaryText={`${passwordError.response.data.message} ${passwordError.message}`}
                />
              )}
              {isPasswordResetSuccess && (
                <SuccessAlert
                  secondaryText={
                    "Password successfully reset, navigate to the signin page and signin"
                  }
                  className="mb-4"
                />
              )}
              <Button
                className="rounded-md text-base shadow-sm hover:opacity-75"
                type="submit"
                disabled={disableUserInterface}
              >
                {isPasswordResetLoading || isFormLoading ? (
                  <div className="w-full h-full flex justify-center items-center">
                    <ThreeDots visible width="40" height="20" color="#000" />
                  </div>
                ) : (
                  "Reset password"
                )}
              </Button>
            </div>
          </form>
        </div>
        <div className="mb-20" />
      </main>
    </LoadPageAnimation>
  );
}
