import React from "react";
import { cn } from "../../../utils/custom_functions";

//
export function SuccessAlert({ secondaryText, className, children, ...props }) {
  return (
    <div
      {...props}
      className={cn(
        "bg-green-100 border-t-4 border-green-500 rounded-b-md text-green-900 px-4 py-2",
        className
      )}
      role="alert"
    >
      <div className="flex text-wrap break-words">
        <div>
          <p className="font-bold">Yey ! all set</p>
          <p className="text-sm text-wrap break-words">
            {secondaryText
              ? secondaryText
              : "Welcome to Matols Logistics services"}
          </p>
        </div>
      </div>
    </div>
  );
}

export function ErrorAlert({ secondaryText, className, children, ...props }) {
  return (
    <div
      {...props}
      className={cn(
        "bg-red-100 border-t-4 border-red-500 rounded-b-md text-red-900 px-4 py-2",
        className
      )}
      role="alert"
    >
      <div className="flex">
        <div>
          <p className="font-bold">Oops ! Error</p>
          <p className="text-sm">
            {secondaryText ? secondaryText : "An error occured"}
          </p>
        </div>
      </div>
    </div>
  );
}
