import { motion } from "framer-motion";
import { PAGES_VARIANT } from "../../utils/animation_variants";
import {
  SECTION_ANIMATION_DELAY,
  SECTION_ANIMATION_DURATION,
} from "../../utils/information";

export function LoadPageAnimation({ children, ...props }) {
  return (
    <motion.section
      variants={PAGES_VARIANT}
      animate="visible"
      initial="hidden"
      exit="exit"
      transition={{
        duration: SECTION_ANIMATION_DURATION,
        delay: SECTION_ANIMATION_DELAY,
      }}
    >
      {children}
    </motion.section>
  );
}
